"use client"

import { useState } from "react"
import { Card, CardContent } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Textarea } from "@/components/ui/textarea"
import { Badge } from "@/components/ui/badge"
import { Dialog, DialogContent, DialogFooter, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog"
import { Plus, Edit, Trash2, AlertCircle } from "lucide-react"
import Image from "next/image"

export interface Product {
  id: string
  name: string
  description: string
  price: number
  stock: number
  category: string
  salonId?: string
  imageUrl?: string
  status: "active" | "draft"
}

interface ProductsTabProps {
  products: Product[]
  onAddProduct: (product: Omit<Product, "id" | "status">) => void
  onEditProduct: (product: Product) => void
  onDeleteProduct: (id: string) => void
}

export function ProductsTab({ products, onAddProduct, onEditProduct, onDeleteProduct }: ProductsTabProps) {
  const [newProduct, setNewProduct] = useState({ name: "", description: "", price: 0, stock: 0, category: "Soins" })
  const [editingProduct, setEditingProduct] = useState<Product | null>(null)

  const handleAddProduct = () => {
    onAddProduct(newProduct)
    setNewProduct({ name: "", description: "", price: 0, stock: 0, category: "Soins" })
  }

  const handleUpdateProduct = () => {
    if (!editingProduct) return
    onEditProduct(editingProduct)
    setEditingProduct(null)
  }

  return (
    <div className="space-y-6">
      <div className="flex justify-between">
        <h2 className="text-2xl font-bold">Gestion de la boutique</h2>
        <Dialog>
          <DialogTrigger asChild>
            <Button>
              <Plus className="w-4 h-4 mr-2" /> Ajouter un produit
            </Button>
          </DialogTrigger>
          <DialogContent>
            <DialogHeader>
              <DialogTitle>Ajouter un produit</DialogTitle>
            </DialogHeader>
            <div className="space-y-4">
              <div>
                <Label>Nom du produit</Label>
                <Input
                  value={newProduct.name}
                  onChange={(e) => setNewProduct({ ...newProduct, name: e.target.value })}
                />
              </div>
              <div>
                <Label>Description</Label>
                <Textarea
                  value={newProduct.description}
                  onChange={(e) => setNewProduct({ ...newProduct, description: e.target.value })}
                />
              </div>
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <Label>Prix (€)</Label>
                  <Input
                    type="number"
                    step="0.01"
                    value={newProduct.price}
                    onChange={(e) => setNewProduct({ ...newProduct, price: Number.parseFloat(e.target.value) })}
                  />
                </div>
                <div>
                  <Label>Stock</Label>
                  <Input
                    type="number"
                    value={newProduct.stock}
                    onChange={(e) => setNewProduct({ ...newProduct, stock: Number.parseInt(e.target.value) })}
                  />
                </div>
              </div>
              <div>
                <Label>Catégorie</Label>
                <Select
                  value={newProduct.category}
                  onValueChange={(v) => setNewProduct({ ...newProduct, category: v })}
                >
                  <SelectTrigger>
                    <SelectValue />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="Soins">Soins</SelectItem>
                    <SelectItem value="Accessoires">Accessoires</SelectItem>
                    <SelectItem value="Coiffure">Coiffure</SelectItem>
                  </SelectContent>
                </Select>
              </div>
            </div>
            <DialogFooter>
              <Button onClick={handleAddProduct}>Créer le produit</Button>
            </DialogFooter>
          </DialogContent>
        </Dialog>
      </div>

      {products.some((p) => p.stock < 10) && (
        <Card className="border-yellow-200 bg-yellow-50">
          <CardContent className="p-4 flex items-center gap-2">
            <AlertCircle className="w-5 h-5 text-yellow-600" />
            <p className="text-sm text-yellow-800">
              Attention: {products.filter((p) => p.stock < 10).length} produit(s) avec un stock faible
            </p>
          </CardContent>
        </Card>
      )}

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {products.map((p) => (
          <Card key={p.id}>
            <CardContent className="p-4">
              {p.imageUrl && (
                <div className="relative w-full h-40 mb-4 rounded-lg overflow-hidden bg-muted">
                  <Image src={p.imageUrl || "/placeholder.svg"} alt={p.name} fill className="object-cover" />
                </div>
              )}
              <div className="space-y-2">
                <div className="flex items-start justify-between">
                  <h3 className="font-semibold">{p.name}</h3>
                  <Badge variant={p.stock < 10 ? "destructive" : "default"}>Stock: {p.stock}</Badge>
                </div>
                <p className="text-sm text-muted-foreground line-clamp-2">{p.description}</p>
                <div className="flex items-center justify-between">
                  <span className="text-lg font-bold">€{p.price.toFixed(2)}</span>
                  <Badge variant="outline">{p.category}</Badge>
                </div>
                <div className="flex gap-2 pt-2">
                  <Dialog>
                    <DialogTrigger asChild>
                      <Button
                        variant="outline"
                        size="sm"
                        className="flex-1 bg-transparent"
                        onClick={() => setEditingProduct(p)}
                      >
                        <Edit className="w-4 h-4 mr-1" /> Modifier
                      </Button>
                    </DialogTrigger>
                    <DialogContent>
                      <DialogHeader>
                        <DialogTitle>Modifier le produit</DialogTitle>
                      </DialogHeader>
                      {editingProduct && (
                        <div className="space-y-4">
                          <div>
                            <Label>Nom du produit</Label>
                            <Input
                              value={editingProduct.name}
                              onChange={(e) => setEditingProduct({ ...editingProduct, name: e.target.value })}
                            />
                          </div>
                          <div>
                            <Label>Description</Label>
                            <Textarea
                              value={editingProduct.description}
                              onChange={(e) => setEditingProduct({ ...editingProduct, description: e.target.value })}
                            />
                          </div>
                          <div className="grid grid-cols-2 gap-4">
                            <div>
                              <Label>Prix (€)</Label>
                              <Input
                                type="number"
                                step="0.01"
                                value={editingProduct.price}
                                onChange={(e) =>
                                  setEditingProduct({ ...editingProduct, price: Number.parseFloat(e.target.value) })
                                }
                              />
                            </div>
                            <div>
                              <Label>Stock</Label>
                              <Input
                                type="number"
                                value={editingProduct.stock}
                                onChange={(e) =>
                                  setEditingProduct({ ...editingProduct, stock: Number.parseInt(e.target.value) })
                                }
                              />
                            </div>
                          </div>
                          <div>
                            <Label>Catégorie</Label>
                            <Select
                              value={editingProduct.category}
                              onValueChange={(v) => setEditingProduct({ ...editingProduct, category: v })}
                            >
                              <SelectTrigger>
                                <SelectValue />
                              </SelectTrigger>
                              <SelectContent>
                                <SelectItem value="Soins">Soins</SelectItem>
                                <SelectItem value="Accessoires">Accessoires</SelectItem>
                                <SelectItem value="Coiffure">Coiffure</SelectItem>
                              </SelectContent>
                            </Select>
                          </div>
                        </div>
                      )}
                      <DialogFooter>
                        <Button onClick={handleUpdateProduct}>Enregistrer</Button>
                      </DialogFooter>
                    </DialogContent>
                  </Dialog>
                  <Button
                    variant="outline"
                    size="sm"
                    className="text-destructive bg-transparent"
                    onClick={() => onDeleteProduct(p.id)}
                  >
                    <Trash2 className="w-4 h-4" />
                  </Button>
                </div>
              </div>
            </CardContent>
          </Card>
        ))}
      </div>
    </div>
  )
}
