"use client"

import { Button } from "@/components/ui/button"
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"
import { BarChart3, Users, Building2, ShoppingBag, Calendar, Activity, Settings, X } from "lucide-react"
import type { User } from "@/lib/auth-store"

interface SidebarProps {
  user: User | null
  activeTab: string
  sidebarOpen: boolean
  onTabChange: (tab: string) => void
  onClose: () => void
}

export function Sidebar({ user, activeTab, sidebarOpen, onTabChange, onClose }: SidebarProps) {
  return (
    <aside
      className={`${sidebarOpen ? "translate-x-0" : "-translate-x-full"} fixed inset-y-0 left-0 z-50 w-64 bg-white dark:bg-gray-900 border-r border-border transition-transform duration-300 ease-in-out lg:translate-x-0 lg:static lg:inset-0`}
    >
      <div className="flex h-full flex-col">
        <div className="flex h-16 items-center justify-between px-6 border-b">
          <div className="flex items-center gap-3">
            <div className="w-10 h-10 rounded-full bg-primary flex items-center justify-center text-white font-bold">
              NL
            </div>
            <div>
              <h1 className="text-xl font-bold">NappyLocks</h1>
              <p className="text-xs text-muted-foreground">SuperAdmin</p>
            </div>
          </div>
          <Button variant="ghost" size="icon" className="lg:hidden" onClick={onClose}>
            <X className="w-5 h-5" />
          </Button>
        </div>
        <nav className="flex-1 space-y-2 p-4">
          <Button
            variant={activeTab === "overview" ? "secondary" : "ghost"}
            className="w-full justify-start gap-3"
            onClick={() => onTabChange("overview")}
          >
            <BarChart3 className="w-5 h-5" /> Dashboard
          </Button>
          <Button
            variant={activeTab === "users" ? "secondary" : "ghost"}
            className="w-full justify-start gap-3"
            onClick={() => onTabChange("users")}
          >
            <Users className="w-5 h-5" /> Utilisateurs
          </Button>
          <Button
            variant={activeTab === "salons" ? "secondary" : "ghost"}
            className="w-full justify-start gap-3"
            onClick={() => onTabChange("salons")}
          >
            <Building2 className="w-5 h-5" /> Salons
          </Button>
          <Button
            variant={activeTab === "products" ? "secondary" : "ghost"}
            className="w-full justify-start gap-3"
            onClick={() => onTabChange("products")}
          >
            <ShoppingBag className="w-5 h-5" /> Boutique
          </Button>
          <Button
            variant={activeTab === "bookings" ? "secondary" : "ghost"}
            className="w-full justify-start gap-3"
            onClick={() => onTabChange("bookings")}
          >
            <Calendar className="w-5 h-5" /> Réservations
          </Button>
          <Button
            variant={activeTab === "logs" ? "secondary" : "ghost"}
            className="w-full justify-start gap-3"
            onClick={() => onTabChange("logs")}
          >
            <Activity className="w-5 h-5" /> Logs système
          </Button>
          <Button
            variant={activeTab === "settings" ? "secondary" : "ghost"}
            className="w-full justify-start gap-3"
            onClick={() => onTabChange("settings")}
          >
            <Settings className="w-5 h-5" /> Paramètres
          </Button>
        </nav>
        <div className="border-t p-4">
          <div className="flex items-center gap-3">
            <Avatar>
              <AvatarImage src={user?.avatar || "/placeholder.svg"} />
              <AvatarFallback>{user?.username[0]}</AvatarFallback>
            </Avatar>
            <div>
              <p className="font-medium">{user?.username}</p>
              <p className="text-xs text-muted-foreground">SuperAdmin</p>
            </div>
          </div>
        </div>
      </div>
    </aside>
  )
}
