export default function SuperAdminDashboard() {
  const router = useRouter()
  const { toast } = useToast()
  const { user, isAuthenticated } = useAuthStore()

  const [users, setUsers] = useState([
    {
      _id: "1",
      email: "superadmin@nappylocks.com",
      username: "Super Admin",
      role: "superadmin",
      createdAt: "2024-01-01",
    },
    { _id: "2", email: "admin@nappylocks.com", username: "Sophie Admin", role: "admin", createdAt: "2025-01-05" },
    { _id: "3", email: "manager@nappylocks.com", username: "Jean Manager", role: "manager", createdAt: "2025-01-10" },
  ])
  const [salons, setSalons] = useState(mockSalons)
  const [products, setProducts] = useState(mockProducts)
  const [bookings, setBookings] = useState(mockBookings)
  const [logs, setLogs] = useState(mockLogs)

  const [sidebarOpen, setSidebarOpen] = useState(false)
  const [activeTab, setActiveTab] = useState("overview")

  const [newUser, setNewUser] = useState({ username: "", email: "", password: "", role: "manager" })
  const [newSalon, setNewSalon] = useState({ name: "", address: "", city: "", phone: "", email: "", managerId: "" })
  const [newProduct, setNewProduct] = useState({ name: "", description: "", price: 0, stock: 0, category: "Soins" })
  const [editingUser, setEditingUser] = useState(null)
  const [editingSalon, setEditingSalon] = useState(null)
  const [editingProduct, setEditingProduct] = useState(null)

  const [userFilter, setUserFilter] = useState("all")
  const [bookingFilter, setBookingFilter] = useState("all")
  const [logFilter, setLogFilter] = useState("all")
  const [searchTerm, setSearchTerm] = useState("")

  useEffect(() => {
    if (!isAuthenticated || user?.role !== "superadmin") {
      router.push("/auth/login")
    }
  }, [isAuthenticated, user, router])

  const handleAddUser = (newUser) => {
    const created = {
      _id: Date.now().toString(),
      username: newUser.username,
      email: newUser.email,
      role: newUser.role,
      createdAt: new Date().toISOString(),
    }
    setUsers([...users, created])
    toast({ title: "Succès", description: "Utilisateur ajouté avec succès" })
  }

  const handleEditUser = (updatedUser) => {
    setUsers(users.map((u) => (u._id === updatedUser._id ? updatedUser : u)))
    toast({ title: "Succès", description: "Utilisateur modifié avec succès" })
  }

  const handleDeleteUser = (id) => {
    if (users.find((u) => u._id === id)?.role === "superadmin") {
      toast({ title: "Erreur", description: "Impossible de supprimer un superadmin", variant: "destructive" })
      return
    }
    setUsers(users.filter((u) => u._id !== id))
    toast({ title: "Supprimé", description: "Utilisateur supprimé" })
  }

  const handleAddSalon = (newSalon) => {
    const created = {
      id: Date.now().toString(),
      ...newSalon,
      status: "active",
      createdAt: new Date().toISOString(),
    }
    setSalons([...salons, created])
    toast({ title: "Succès", description: "Salon ajouté avec succès" })
  }

  const handleEditSalon = (updatedSalon) => {
    setSalons(salons.map((s) => (s.id === updatedSalon.id ? updatedSalon : s)))
    toast({ title: "Succès", description: "Salon modifié avec succès" })
  }

  const handleDeleteSalon = (id) => {
    setSalons(salons.filter((s) => s.id !== id))
    toast({ title: "Supprimé", description: "Salon supprimé" })
  }

  const handleToggleSalonStatus = (id) => {
    setSalons(salons.map((s) => (s.id === id ? { ...s, status: s.status === "active" ? "inactive" : "active" } : s)))
    toast({ title: "Statut modifié", description: "Le statut du salon a été mis à jour" })
  }

  const handleAddProduct = (newProduct) => {
    const created = {
      id: Date.now().toString(),
      ...newProduct,
      status: "active",
    }
    setProducts([...products, created])
    toast({ title: "Succès", description: "Produit ajouté avec succès" })
  }

  const handleEditProduct = (updatedProduct) => {
    setProducts(products.map((p) => (p.id === updatedProduct.id ? updatedProduct : p)))
    toast({ title: "Succès", description: "Produit modifié avec succès" })
  }

  const handleDeleteProduct = (id) => {
    setProducts(products.filter((p) => p.id !== id))
    toast({ title: "Supprimé", description: "Produit supprimé" })
  }

  const handleUpdateBookingStatus = (id, status) => {
    setBookings(bookings.map((b) => (b.id === id ? { ...b, status } : b)))
    toast({ title: "Statut modifié", description: "La réservation a été mise à jour" })
  }

  const handleExportUsers = () => {
    toast({ title: "Export en cours", description: "Les données utilisateurs sont en cours d'export..." })
  }

  const handleExportSalons = () => {
    toast({ title: "Export en cours", description: "Les données des salons sont en cours d'export..." })
  }

  const handleRefresh = () => {
    toast({ title: "Rafraîchissement", description: "Les données ont été rafraîchies" })
  }

  if (!isAuthenticated || user?.role !== "superadmin") return null

  const filteredUsers = users.filter((u) => {
    const matchesSearch =
      u.username.toLowerCase().includes(searchTerm.toLowerCase()) ||
      u.email.toLowerCase().includes(searchTerm.toLowerCase())
    const matchesFilter = userFilter === "all" || u.role === userFilter
    return matchesSearch && matchesFilter
  })

  const filteredBookings = bookings.filter((b) => bookingFilter === "all" || b.status === bookingFilter)

  const filteredLogs = logs.filter((l) => logFilter === "all" || l.type === logFilter)

  const getRoleColor = (role) => {
    switch (role) {
      case "superadmin":
        return "bg-purple-100 text-purple-800 border-purple-200"
      case "admin":
        return "bg-blue-100 text-blue-800 border-blue-200"
      case "manager":
        return "bg-green-100 text-green-800 border-green-200"
      default:
        return "bg-gray-100 text-gray-800 border-gray-200"
    }
  }

  const getStatusColor = (status) => {
    switch (status) {
      case "active":
      case "confirmed":
      case "completed":
        return "bg-green-100 text-green-800"
      case "pending":
        return "bg-yellow-100 text-yellow-800"
      case "inactive":
      case "cancelled":
        return "bg-red-100 text-red-800"
      default:
        return "bg-gray-100 text-gray-800"
    }
  }

  const getLogIcon = (type) => {
    switch (type) {
      case "success":
        return <CheckCircle className="w-4 h-4 text-green-600" />
      case "error":
        return <AlertCircle className="w-4 h-4 text-red-600" />
      case "warning":
        return <AlertCircle className="w-4 h-4 text-yellow-600" />
      case "info":
        return <Activity className="w-4 h-4 text-blue-600" />
    }
  }

  return (
    <div className="flex h-screen bg-background">
      <Sidebar
        user={user}
        activeTab={activeTab}
        sidebarOpen={sidebarOpen}
        onTabChange={setActiveTab}
        onClose={() => setSidebarOpen(false)}
      />

      <div className="flex-1 flex flex-col">
        <Header>
          <Button variant="ghost" size="icon" className="lg:hidden" onClick={() => setSidebarOpen(true)}>
            <Menu className="w-5 h-5" />
          </Button>
        </Header>

        <main className="flex-1 overflow-y-auto p-6">
          <div className="max-w-7xl mx-auto">
            <div className="mb-8">
              <h1 className="text-4xl font-bold">Tableau de bord SuperAdmin</h1>
              <p className="text-muted-foreground">Gestion complète de la plateforme NappyLocks</p>
            </div>

            {activeTab === "overview" && (
              <>
                <div className="mb-8">
                  <StatsCards users={users} salons={salons} monthlyRevenue={48520} todayBookings={87} />
                </div>
                <OverviewTab />
              </>
            )}

            {activeTab === "users" && (
              <UsersTa
                users={users}
                onAddUser={handleAddUser}
                onEditUser={handleEditUser}
                onDeleteUser={handleDeleteUser}
                onExport={handleExportUsers}
              />
            )}

            {activeTab === "salons" && (
              <SalonsTab
                salons={salons}
                users={users}
                onAddSalon={handleAddSalon}
                onEditSalon={handleEditSalon}
                onDeleteSalon={handleDeleteSalon}
                onToggleStatus={handleToggleSalonStatus}
                onExport={handleExportSalons}
              />
            )}

            {activeTab === "products" && (
              <ProductsTab
                products={products}
                onAddProduct={handleAddProduct}
                onEditProduct={handleEditProduct}
                onDeleteProduct={handleDeleteProduct}
              />
            )}

            {activeTab === "bookings" && <BookingsTab bookings={bookings} onUpdateStatus={handleUpdateBookingStatus} />}

            {activeTab === "logs" && <LogsTab logs={logs} />}

            {activeTab === "settings" && <SettingsTab onRefresh={handleRefresh} />}
          </div>
        </main>
      </div>
    </div>
  )
}
